require('dotenv').config();
const axios = require('axios');
const port = process.env.apiPORT;
console.log(port);
const apis = 'http://localhost:8000/api';
let detail = (question) => {
  return axios.post(apis + '/result', question);
};
let categories = () => {
  return fetch(apis + '/categories')
    .then(res => res.json());
};
let pageChild = (page) => {
  return axios.post(apis + '/page', page);
};
module.exports = {
  detail, categories, pageChild
};